import {
	uniPopup
} from '@dcloudio/uni-ui'
import Topbar from '../../components/topBar/topbarx.vue'
export default {
	data() {
		return {
			title: '个人信息',
			topbarData: {
				leftHandle: false,
				title: "个人信息",
				left: "《",
				right: ""
			},
			person:{
				username:"",
				sex:'',
				sexStr:'',
				mobile:"",
				nickname:"", // nickname
				headPic: '../../static/person/por.png'
				
			},
		}
	},
	onLoad(opt){
		
	},
	onUnload(){
		uni.hideLoading()
	},
	onShow(){
		if(this.$store.state.userInfo.type){
			this.person.nickname = this.$store.state.userInfo.nickname
			this.person.username = this.$store.state.userInfo.username
			this.person.sexStr = this.$store.state.userInfo.sex
			this.person.mobile = this.$store.state.userInfo.mobile
			this.person.headPic = this.$store.state.userInfo.avatar
		}
	},
	methods: {
		
		
		// 上传头像
		chooseImg(){
			let _this = this
			uni.showModal({
				title: '提示',
				content: '更改头像？头像格最好为正方形',
				success(res) {
					if(res.confirm){
						console.log('用户确认更换头像')
						
						uni.chooseImage({
							count: 1, // 上传图片的张数
							sourceType: ['album'], //从相册选择
							success(res){
								const tempFilePaths = res.tempFilePaths;
								
								const uploadTask = uni.uploadFile({
									url: _this.$http + '/api/user/uphead',
									// methods: 'POST',
									filePath: tempFilePaths[0],
									name: 'file',
									formData: {
										type: 'yes',
										token: _this.$store.state.userInfo.token
									  },
									  success: function (uploadFileRes) {
									 console.log(uploadFileRes.data);
									 let resObj = JSON.parse( uploadFileRes.data )
									 // console.log(resObj);
									 if(resObj.code ==1){
										 uni.hideLoading()
										 console.log('上传成功')
										 
										 let imgUrl = resObj.data.url // 地址
										 let image = resObj.data.http + resObj.data.url // 完整路径
										 console.log(image,imgUrl)
										 _this.$store.commit('setAvatar', image)
										 _this.person.headPic = image
										 // 更改头像本地缓存
										 uni.setStorage({
										 	key: 'srorage_avatar',
										 	data: image
										 })
									 }
									}
									
								})
							}
							
							
							
							
							
						})
						
						
						
						
						
						
						
					}else{
						console.log('用户取消更改头像')
					}
				}
			})
		},
		// 修改个人信息 
		goModifyPerson(){
			uni.navigateTo({
				url:"../modifyperson/modifyperson"
			})
		},
		
		
		// 选择性别
		// choseSex(){
		// 	this.$refs.popup.open()
		// },
		
		// 设置性别
		setSex(type){
			this.person.sex = type
			type == 0?this.person.sexStr = '男' : this.person.sexStr = '女',
			this.$refs.popup.close()
		},
		
		childbar(val) {
			switch (val.identify) {
				case "leftHandle":
					break;
				case "rightHandle":
					this.$router.push({
						path: "/pwdreg",
						query: {
							tag: "reg",
							btn: "reg"
						}
					});
					break;
			}
		},
		// 退出登陆
		outsigin(){
			let _this = this
			uni.showModal({
				title: '操作提示',
				content: '是否退出当前账号？',
				success(res){
					if(res.confirm){
						_this.$store.commit("setUserInfo", {
							token: '', // token
							username: '点击登录', // 用户名
							avatar: '../../static/mine/notPor.png', // 头像
							score: 0,
							nickName: '',// 昵称
							type: '', // 登陆方式 1三方授权  2账号密码/验证码。
							mobile: '', // 手机号
							sex: '', //性别
							isSigin: false
						})
						// 清除本地缓存
						uni.removeStorage({
							key: 'srorage_token'
						})
						uni.removeStorage({
							key: 'srorage_nickname'
						})
						uni.removeStorage({
							key: 'srorage_username'
						})
						uni.removeStorage({
							key: 'srorage_avatar'
						})
						uni.removeStorage({
							key: 'srorage_score'
						})
						uni.removeStorage({
							key: 'srorage_sex'
						})
						uni.removeStorage({
							key: 'srorage_mobile'
						})
						uni.removeStorage({
							key: 'srorage_type'
						})
						uni.removeStorage({
							key: 'srorage_token'
						})
						uni.removeStorage({
							key: 'srorage_token',
							success(res) {
								console.log('本地缓存清除完毕',res.data)
							}
						})
						
						
						uni.switchTab({
							url: '../mine/mine'
						})
					}else{
						return
					}
				}
			})
			
		}
	},
	mounted() {
	},
	components: {
		Topbar,
		uniPopup
	}
}
